.app {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  height: inherit;
  min-height: 100vh;
}

h1 {
  font-size: 100px;
  font-weight: 700;
  color: #000080;
  text-align: center;
  opacity: 0.55;
  font-family: Roboto, Arial, sans-serif;
  background-color: #2AA5A0;
  background-image: linear-gradient(90deg, #E3535D, #2AA5A0);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transform: rotate(5deg);
}

h2 {
  font-size: 24px;
  color: #af2f2f;
  font-style: italic;
  font-weight: 700;
  opacity: 0.65; }
  @media (min-width: 0px) and (max-width: 988px) {
    h2 {
      font-size: 18px;
      margin-top: 70px; } }

h3 {
  margin: 0;
  font-size: 12px;
  color: #1B1F2B;
  opacity: 0.55; }